.sidebar {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: white;
  color: black;

  .bg {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    
    background-color: white;
    z-index: 10;

    .links {
      position: absolute;
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      gap: 20px;
    }
    a {
      font-size: 40px;
    }
  }

  button {
    width: 50px;
    height: 50px;
    border-radius: 50px;
    position: fixed;
    top: 25px;
    left: 38px;
    background-color: transparent;
    border: none;
    cursor:pointer;
    z-index: 11;
  }
}
